        async updateTerminalBrandingAsset() {
            if (this.correctFormat == true) {
                const selectedDays = [];
                this.daysOfWeek.forEach((day) => {
                    // console.log(day);
                    // console.log(day.value);
                    if (day.value == true) {
                        // console.log(day.name);
                        selectedDays.push(day.name);
                    }
                });
                // console.log(selectedDays);
                // console.log(this.editBrandingAsset.startDate);
                if (this.editBrandingAsset.padded == true) {
                    // console.log('padded is true');
                    this.editBrandingAsset.padded = 1;
                } else {
                    // console.log('padded is false');
                    this.editBrandingAsset.padded = 0;
                }
                if (this.editBrandingAsset.enabled == true) {
                    // console.log('enabled is true');
                    this.editBrandingAsset.enabled = 1;
                } else {
                    // console.log('enabled is false');
                    this.editBrandingAsset.enabled = 0;
                }
                if (this.editBrandingAsset.preview == true) {
                    // console.log('preview is true');
                    this.editBrandingAsset.preview = 1;
                } else {
                    // console.log('preview is false');
                    this.editBrandingAsset.preview = 0;
                }
                if (this.editBrandingAsset.startDate == undefined) {
                    this.editBrandingAsset.startDate = this.item.startDate;
                }
                if (this.editBrandingAsset.endDate == undefined) {
                    this.editBrandingAsset.endDate = this.item.endDate;
                }
                if (this.editBrandingAsset.startTime == undefined) {
                    this.editBrandingAsset.startTime = this.item.startTime;
                }
                if (this.editBrandingAsset.endTime == undefined) {
                    this.editBrandingAsset.endTime = this.item.endTime;
                }
                if (this.editBrandingAsset.notes == undefined) {
                    this.editBrandingAsset.notes = this.item.notes;
                }
                let formData = new FormData();
                formData.append('asset_id', this.editBrandingAsset.assetId);
                formData.append('media_id', this.editBrandingAsset.mediaId);
                formData.append(
                    'slide_show_id',
                    this.editBrandingAsset.slideshowId,
                );
                formData.append('start_date', this.editBrandingAsset.startDate);
                formData.append('end_date', this.editBrandingAsset.endDate);
                formData.append('start_time', this.editBrandingAsset.startTime);
                formData.append('end_time', this.editBrandingAsset.endTime);
                formData.append('notes', this.editBrandingAsset.notes);
                // formData.append('days_of_week[]', selectedDays);
                selectedDays.forEach((day) => {
                    // console.log(day);
                    formData.append('days_of_week[]', day);
                });
                formData.append('padded', this.editBrandingAsset.padded);
                // formData.append('ordinal', 99);
                formData.append('preview', this.editBrandingAsset.preview);
                formData.append('enabled', this.editBrandingAsset.enabled);

                try {
                    let response = await axios.post(
                        `${
                            this.getInternalCoreClearController +
                            '/update_branding_asset'
                        }`,
                        formData,
                        {
                            withCredentials: true,
                            crossDomain: true,
                            headers: {
                                'Content-Type': 'multipart/form-data',
                            },
                        },
                    );
                    console.log(response);
                    if (response.data.success === true) {
                        this.alert = true;
                        this.alertType = 'success';
                        this.alertMessage = 'Asset updated';
                        setTimeout(() => {
                            location.reload();
                        }, 2000);
                    }
                } catch (error) {
                    console.error('Could not update');
                    console.error(error);
                }
            } else {
                this.alert = true;
                this.alertType = 'warning';
                this.alertMessage = 'Invalid format, could not update';
            }
        },
