        async getTerminalBrandingList() {
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/terminal_branding'
                    }`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                if (response.data) {
                    console.log(response.data.activeAsset.id);
                    const isActive = response.data.activeAsset.success;

                    if (isActive) {
                        this.activeAssetId = response.data.activeAsset.id;
                    } else {
                        this.assetAlert = true;
                    }
                    console.log(this.previewTerminal);
                    var slideshowArray = response.data.results;
                    for (var name of slideshowArray) {
                        if (name != '') {
                            this.branding.push(name);
                        }
                    }
                } else {
                    this.branding = [];
                    console.log('Branding list not found!');
                }
            } catch (error) {
                console.error('Could not get branding list');
                console.error(error);
            }
        },

        async addMedia() {
            let formData = new FormData();
            // console.log(this.selectedImages);
            if (this.targetSlideShow) {
                // console.log('slideshow is selected');
                formData.append('slide_show_id', this.targetSlideShow.id);
            } else if (this.selectedImages != '') {
                formData.append('media_id', this.selectedImages.id);
            }
            // else {
            //     console.log('fourth case');
            // }

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/update_branding_asset'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );
                if (response.data) {
                    // console.log('Post');
                    // console.log(this.originalFile[0]);
                    console.log(response.data);
                    this.alert = true;
                    this.alertType = 'success';
                    this.alertMessage = 'Asset added';
                    setTimeout(() => {
                        this.selectedImageUrl = '';
                        location.reload();
                    }, 1500);
                } else {
                    console.log('Branding asset not found!');
                }
            } catch (error) {
                console.error('Could not upload branding asset');
                console.error(error);
            }
            this.reset();
        },
        async deleteBrandingAsset(item) {
            // console.log(item);
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController +
                        '/delete_branding_asset'
                    }`,
                    {
                        params: { asset_id: item.id },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    if (response.data.success === true) {
                        // console.log('deleted');
                        this.imageUsed = false;
                        this.alert = true;
                        this.alertType = 'success';
                        this.alertMessage = 'Asset deleted';
                        setTimeout(() => {
                            location.reload();
                        }, 2000);
                    }
                } else {
                    console.log('Branding asset not found!');
                }
            } catch (error) {
                console.error('Could not delete branding asset');
                console.error(error);
            }
        },
        async getSlideshowList() {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/slide_shows'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    this.selectSlideShow = response.data.results;

                    // console.log(this.selectSlideShow);
                } else {
                    this.selectSlideShow = [];
                    console.log('Slideshow list not found!');
                }
            } catch (error) {
                console.error('Could not get slideshow list');
                console.error(error);
            }
        },
        async getMediaLibrary(input) {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/get_media'}`,
                    {
                        params: { page: input },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                if (response.data) {
                    this.pagesTotal = response.data.pages;
                    const mediaLibrary = response.data.results;
                    // console.log(mediaLibrary);
                    this.library.length = 0;
                    for (let i = 0; i < mediaLibrary.length; i++) {
                        let id = mediaLibrary[i].id;
                        let thumbnailUrl = mediaLibrary[i].thumbnailUrl;

                        this.library.push({
                            id,
                            thumbnailUrl,
                        });
                    }
                } else {
                    this.library = [];
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
                this.alert = true;
                this.alertType = 'error';
                this.alertMessage = error;
            }
        },
        async uploadMedia() {
            // console.log(this.originalFile);
            let formData = new FormData();
            formData.append(
                'upload_media',
                this.originalFile,
                this.originalFile.name,
            );

            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController + '/upload_media'}`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );

                if (response.data) {
                    // console.log(response);
                    this.selectedImages = response.data;
                    this.addMedia();
                } else {
                    console.log('Unable to upload');
                }
            } catch (error) {
                console.error('Could not upload media');
                console.error(error);
            }
        },
