            // console.log(key);
            // console.log(image.thumbnailUrl);
            this.selectedImages = image;
            this.selectedImageUrl = image.thumbnailUrl;
            this.selectedId = image.id;
        },
        slideshowSelection(item) {
            const selectedSlideshowObj = this.selectSlideShow.find(
                (slideshow) => slideshow.name === item,
            );

            if (selectedSlideshowObj) {
                this.targetSlideShow = selectedSlideshowObj;
                // console.log(this.targetSlideShow);
            } else {
                console.log('Selected Slideshow not found!');
