        checkExclusiveValue(value) {
            if (
                value === 'enabled' &&
                this.editBrandingAsset.enabled === true
            ) {
                this.$nextTick(() => {
                    this.editBrandingAsset.preview = false;
                    this.$forceUpdate();
                });
            } else if (
                value === 'preview' &&
                this.editBrandingAsset.preview === true
            ) {
                this.$nextTick(() => {
                    this.editBrandingAsset.enabled = false;
                    this.$forceUpdate();
                });
            } else {
                console.log('problem occurred');
            }
        },

        validateFormat(item) {
            // console.log(item);
            const timePattern = /^$|^(?:[0-9]{1,2}):[0-5][0-9]$/;
            const datePattern =
                /^$|^(?:(?:0[1-9]|1[0-2])\/(0[1-9]|[12][0-9]|3[01])\/\d{4}|(?:\d{4})-(0[1-9]|1[0-2])-(0[1-9]|[12][0-9]|3[01]))$/;

            if (item == 'startTime') {
                if (!timePattern.test(this.editBrandingAsset.startTime)) {
                    this.startTimeError = [
                        'Invalid time format. Use XX:XX or X:XX.',
                    ];
                    this.correctFormat = false;
                } else {
                    this.startTimeError = [];
                    this.correctFormat = true;
                }
            } else if (item == 'endTime') {
                if (!timePattern.test(this.editBrandingAsset.endTime)) {
                    this.endTimeError = [
                        'Invalid time format. Use XX:XX or X:XX.',
                    ];
                    this.correctFormat = false;
                } else {
                    this.endTimeError = [];
                    this.correctFormat = true;
                }
            } else if (item == 'startDate') {
                if (!datePattern.test(this.editBrandingAsset.startDate)) {
                    this.startDateError = [
                        'Invalid date format. Use MM/DD/YYYY or YYYY-MM-DD.',
                    ];
                    this.correctFormat = false;
                } else {
                    this.startDateError = [];
                    this.correctFormat = true;
                }
            } else if (item == 'endDate') {
                if (!datePattern.test(this.editBrandingAsset.endDate)) {
                    this.endDateError = [
                        'Invalid date format. Use MM/DD/YYYY or YYYY-MM-DD.',
                    ];
                    this.correctFormat = false;
                } else {
                    this.endDateError = [];
                    this.correctFormat = true;
                }
            }
            // else {
            //     console.log('fifth condition');
            // }
        },
        reset() {
            this.$refs.uploadForm.reset();
        },
    },
